body {
	background: white;
	color: #6A778B;
	font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
	font-size: 9pt;
	line-height: 1.5;
}
h1, h2, h3, h4, h5, h6 {
	color: #262626;
	font-weight: 500;
}
h3 {
	color: #1D2737;
}
h4, h5, h6 {
	color: #30405B;
}

@media screen and (min-width: 23em) {
	body {
		font-size: 10pt;
	}
}
@media screen and (min-width: 25em) {
	body {
		font-size: 11pt;
	}
}
@media screen and (min-width: 43em) {
	body {
		font-size: 12pt;
	}
}
a:link,
a:visited {
	color: #2E8BE0;
	text-decoration: none;
}
a:hover {
	text-decoration: underline;
}
a:active {
	text-decoration: none;
}
.nowrap {
	white-space: nowrap;
}

.q-header {
	margin: 0 auto;
	max-width: 1430px;
}
.q-header__container {
	margin: 1.5em 5%;
}
.q-header__logo,
.q-header__nav,
.q-header__backlink {
	display: block;
}
.q-header__logo {
	flex: 1 0 auto;
	max-height: 57px;
}
.q-header__logo svg {
	display: block;
	max-height: 57px;
	max-width: 402px;
}
.q-header__nav {
	display: flex;
	font-weight: 500;
	justify-content: space-around;
	list-style: none;
	margin: 1.5em 0;
	padding: 0;
}
.q-header__nav a:link,
.q-header__nav a:visited {
	color: rgb(98, 114, 129);
	text-decoration: none;
	transition: color 0.4s;
}
.q-header__nav a:hover {
	color: rgb(52, 56, 59)
}
.q-header__backlink {
	font-size: 9pt;
	height: auto;
	position: absolute;
	right: 5%;
	top: 1em;
	width: auto;
	z-index: 1;
}
@media (min-width: 694px) {
	.q-header__backlink {
		top: 1.25em;
	}
}
@media (min-width: 1430px) {
	.q-header__backlink {
		right: calc(50% - 1430px / 2 + 1430px * 0.05);
	}
}


/*
 * community body styles
 */
.q-sub-header {
	background-color: #2C75BE;
}
.q-sub-header__container {
	margin-left: auto;
	margin-right: auto;
	max-width: 1430px;
}
.q-body {
	margin-left: auto;
	margin-right: auto;
	max-width: 1430px;
}
.q-body__container {
	margin: 0 5%;
}
.q-community-headline {
	color: white;
	font-size: 1.5em;
	font-size: calc(25 / 16 * 1em); /* temp to match blog */
	margin: 0 0 0.75em;
	padding: calc(2 / 3 * 1em) 5%;
}
.q-section {
	margin: 5em 0;
}
.q-section--first {
	margin-top: 0;
}
.q-section-opening {
	margin-bottom: 5em;
}
.q-section-opening .q-section:last-child {
	margin-bottom: 0;
}

/*
 * follow us on social media
 */
.footer-social {
	background-color: rgb(247, 250, 252);
}
.footer-social__content {
	padding: 5em 0;
}
.footer-social__heading {
	color: #262626;
	font-size: 1.8em;
	text-align: center;
}
.q-social-list {
	display: flex;
	list-style: none;
	justify-content: center;
	padding: 0;
}
.q-social-list__item {
	display: inline-flex;
	margin: 0.625em 0.5em 0 0;
}
.q-social-list__item a {
	align-items: center;
	border-radius: 50%;
	border: 1px solid #6CA3CF;
	display: flex;
	height: 3em;
	justify-content: center;
	transition: border-color 0.4s;
	width: 3em;
}
.q-social-list__item a:hover {
	border-color: #707D90;
}
.q-social-list__icon {
	width: 1.25em;
	height: 1.25em;
}
.q-social-list__icon path {
	fill: #707D90;
}

/*
 * qualys footer styles
 */
.q-footer {
	background: #1D2737;
	border-bottom: 6px solid #ED2E26;
}
.q-footer__container {
	margin-left: auto;
	margin-right: auto;
	padding: 5em 5%;
	max-width: 1430px;
}
.q-footer__group {
	margin-bottom: 1.5em;
}
.q-footer__subheading {
	color: white;
}
.q-footer__subheading a {
	color: white;
}
.q-footer__list {
	list-style: none;
	padding-left: 0;
}
a.q-footer__link {
	color: #6A778B;
}
a.q-footer__link:hover {
	color: white;
	text-decoration: none;
}


@media (min-width: 43em) {
	/*
	 * move navigation up next to logo when there is room
	 */
	.q-header__container {
		align-items: center;
		display: flex;
		justify-content: space-between;
	}
	.q-header__nav {
		margin: 0;
	}
	.q-header__nav a {
		margin-left: 2em;
	}

	/*
	 * show opening sections next to each other
	 */
	.q-section-opening {
		display: flex;
	}
	.q-section-opening .q-section {
		flex: 0 1 calc(50% - 2em);
		margin: 0;
	}
	.q-section-opening .q-section:nth-child(2n) {
		margin-left: 2em;
	}

	.q-section:last-child {
		margin-bottom: 5em;
	}
}


/*
 * search box styles
 */
.q-search__container {
	background-color: white;
	border-bottom: 1px solid #EAEAEA;
	border-top: 1px 1px rgba(0, 0, 0, 0.1);
	margin: -1.2em 0 2.5em;
}
.q-search {
	margin: 0 auto;
	max-width: 1430px;
}
.q-search__inner {
	align-items: center;
	display: flex;
	padding: 0 5%;
}
.q-search__label {
	background: none;
	border: 0 none;
	display: inline-block;
	font-size: 2.5em;
	margin-right: 0.38em;
	text-align: center;
	transform: rotate(315deg);
}
.q-search__input {
	border: 0 none;
	box-shadow: none;
	flex: 1;
	font-size: 1.6em;
	line-height: 1.2em;
	margin: 22px 0 17px;
	outline: none;
	padding: 0;
}
.q-search__input:focus {
	border-color: transparent;
}
.q-search__close {
	background: none;
	border: 0 none;
	cursor: pointer;
	font-size: 2em;
	margin-left: 0.5em;
	padding: 0;
}
.q-search__label,
.q-search__input,
.q-search__close {
	color: #AAA;
	transition: color 0.4s, opacity 0.4s;
}
.q-search__label.focus,
.q-search__input.focus,
.q-search__close.focus,
.q-search__label:hover {
	color: inherit;
}
.q-search__close {
	opacity: 0;
}
.q-search__close.focus {
	opacity: 1;
}
